@import "helpers/mixins";
@import "helpers/helpers";
@import "helpers/fonts";
@import "helpers/variables";
@import "lib/ps";


.map {
    width: 100%;
    // height: 1050px
    // height: 0
    // padding-top: 56.25%
    background-color: $black;
    position: relative;
    overflow: hidden;
    height: 100vh;
    // +desktop-less-lg
    //     height: 720px
    // +desktop-less-md
    //     height: 768px
    // +desktop-less-sm
    //     height: 822px
    // +mob-less-lg
    //     height: 655px
    &__in {
        position: absolute;
        top: 20px;
        left: 20px;
        width: calc(100% - 40px);
        height: calc(100% - 40px);
        border-radius: 5px;
        overflow: hidden;
        @include gpu;
        @include tablet-less-sm {
            width: calc(100% - 16px);
            height: calc(100% - 16px);
            top: 8px;
            left: 8px; } }
    &__close {
        position: absolute;
        top: 20px;
        right: calc((100% - 1920px)/2 + 152px);
        background: $orange;
        width: 45px;
        height: 82px;
        @include r(1920) {
            right: percentage(152/1920); }
        @include desktop-less-xl {
            right: percentage(132/1680); }
        @include desktop-less-lg {
            right: percentage(100/1280); }
        @include desktop-less-md {
            right: percentage(80/1024); }
        @include desktop-less-sm {
            right: percentage(30/992); }
        @include tablet-less-lg {
            right: percentage(40/768); }
        @include tablet-less-md {
            right: percentage(20/720) + (100% - percentage(20/720)*2)/16;
            width: 40px; }
        @include tablet-less-sm {
            right: calc(100%/16 + 8px);
            width: 34px;
            top: 8px; }
        @include mob-less-lg {
            width: 21px;
            height: 44px; }

        .icon {
            width: 22px;
            height: 22px;
            fill: #000;
            position: absolute;
            bottom: 10px;
            left: 50%;
            transform: translateX(-50%);
            @include tablet-less-sm {
                bottom: 8px; }
            @include mob-less-lg {
                width: 14px;
                height: 14px;
                bottom: 4px; } }
        // +tablet-less-sm
        //     right: 50%
        //     transform: translate(-50%, 0)
        // +mob-less-lg
        //     max-width: 317px
        //     bottom: 25px
        // +mob-less-md
 } }        //     max-width: 290px

.map-popup {
    box-shadow: 0 0 26px rgba(154, 153, 153, 0.45);
    z-index: 3;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    max-width: 409px;
    //height: percentage(692/1010)
    //max-height: 746px
    //height: 100%
    height: 600px;
    //max-height: percentage(600/1080)
    min-height: 400px;

    position: absolute;
    //top: 50%
    top: 16vh;

    left: calc((100% - 1920px)/2 + 152px);
    // margin-left: -17px
    box-sizing: border-box;
    border-radius: 5px;
    overflow: hidden;
    background: #fff;
    border: 1px solid $orange;
    transition: opacity .25s ease-in-out, visibility .25s 0s;
    // transform: scale(0.9) translateY(-50%)
    @include r(1920) {
        left: percentage(152/1920); }
    @include desktop-less-xl {
        left: percentage(132/1680);
        top: 14vh; }
    @include desktop-less-lg {
        left: percentage(100/1280);
        top: 8vh;
        height: 550px;

        //height: percentage(596/720)
 }        // margin-left: 0
    @include desktop-less-md {
        left: percentage(80/1024);
        height: 540px;
        top: 50%;
        transform: translateY(-50%);
        //max-height: 550px
 }        //height: percentage(690/768)
    @include desktop-less-sm {
        //top: 20vh
        left: percentage(30/992) + (100% - percentage(30/992)*2)/16;
 }        //height: percentage(690/820)
    @include tablet-less-lg {
        //top: 15vh
        left: percentage(40/768) + (100% - percentage(40/768)*2)/16; }
    @include tablet-less-md {
        left: percentage(20/720) + (100% - percentage(20/720)*2)/16;
        min-height: 1px;
 }        //top: 20vh
    @include rorient(767, landscape) {
        left: 50%;
        transform: translate(-50%, 0);
        top: auto;
        bottom: 34px;
        height: auto;
        max-width: 435px; }

    @include tablet-less-sm {
        left: 50%;
        transform: translate(-50%, 0);
        top: auto;
        bottom: 54px;
        height: auto;
        max-width: 435px; }
    @include mob-less-lg {
        max-width: 317px;
        bottom: 36px; }
    @include mob-less-md {
        max-width: 290px; }
    &.is-active {
        opacity: 1;
        visibility: visible;
 }        // transform: scale(1) translateY(-50%)

    &--small-info {
        max-height: 540px;

        // min-height: 55%
        @include tablet-less-md {
            min-height: 1px; }
        .marker-popup__header {
            height: auto;
            min-height: 1px;
            flex-shrink: 0;
            // height: 132px
            // min-height: 132px
            // +tablet-less-sm
            //     height: auto
 }            //     min-height: 1px
        .marker-popup__mob-title-info, .marker-popup__mob-content {
            display: none;
            margin-top: 15px;
            @include tablet-less-sm {
                display: block; }
            .marker-popup__list {
                li {
                    margin-bottom: 3px;
                    @include mob-less-lg {
                        font-size: 16px; }
                    &:last-of-type {
                        margin-bottom: 0; } }
                strong {
                    display: inline-block;
                    margin-right: 5px;
                    @include mob-less-lg {
                        font-size: 16px; } } } }

        .marker-popup__body {
            // height: calc(100% - 132px)
            height: auto; } } }

