.mapboxgl-popup-tip {
    display: none; }
.mapboxgl-popup-content {
    padding: 0;
    width: 100%; }
.mapboxgl-popup-close-button {
    display: none; }
.mapboxgl-canvas-container {
    border-radius: 5px;
    overflow: hidden; }
.mapboxgl-canvas {
    border-radius: 5px; }
.mapboxgl-control-container {
    @include tablet-less-sm {
        display: none; } }
.mapboxgl-ctrl-bottom-right {
    border-bottom-right-radius: 5px;
    overflow: hidden; }

$headerHeight: 140px;
$headerHeightOffset: calc(100% - 141px);
.marker-popup {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    @include gpu;
    &__header {
        display: flex;
        min-height: $headerHeight;
        height: $headerHeight; }
    &__head {
        flex-grow: 1;
        padding: 30px 30px;
        position: relative;
        @include tablet-less-md {
            padding-left: 23px; }
        @include tablet-less-sm {
            padding-top: 25px;
            padding-bottom: 25px;
            padding-left: 46px; }
        @include mob-less-lg {
            padding-left: 30px; }
        h6 {
            font-size: 26px;
            line-height: 1.08;

            @include mob-less-lg {
                font-size: 20px;
                line-height: 1.2; } }
        p {
            @include hm-l;
            font-size: 16px;
            line-height: 1.2;
            &:not(:empty) {
                margin-top: 10px;
                @include mob-less-lg {
                    margin-top: 5px; } } } }
    &__close {
        background: none;
        border-radius: 0;
        appearance: none;
        border: 0;
        position: absolute;
        right: 8px;
        top: 8px;
        display: none;
        @include rorient(767, landscape) {
            display: block; }
        @include tablet-less-sm {
            display: block; }
        .icon {
            width: 20px;
            height: 20px; } }
    &__btns {
        width: 72px;
        flex-shrink: 0;
        flex-grow: 0; }
    &__btn {
        display: block;
        width: 100%;
        text-align: left;
        background: $orange;
        //padding: 15px 9px 20px
        padding: 15px 9px 10px;
        margin-bottom: 2px;
        height: 100%;
        color: black;
        text-decoration: none;
        &--download {
            .marker-popup__btn-text {
                margin-bottom: 12px; } }
        &--case {
            .marker-popup__btn-text {
                margin-bottom: 10px; } }
        @include desktop-less-lg {
            padding-top: 16px; }
        @include tablet-less-md {
            //padding: 16px 6px 10px 10px
            padding: 16px 7px 10px 7px; }
        @include tablet-less-sm {
 }            //height: 50%


        &:not(.button):active,
        &:not(.button):hover {
            color: black;
            @include rmin(1025) {
                .marker-popup__btn-svg {
                    svg {
                        path {
                            fill: #fff; } } } } }
        &-text {
            max-width: 54px;
            display: block;
            font-size: 12px;
            @include hm-sb;
            line-height: 1.1;
            margin-bottom: 17px;
            @include tablet-less-md {
                font-size: 10px;
                line-height: 1.1;
 } }                //margin-bottom: 19px

        &-svg {
            position: relative;
            padding-bottom: 4px;
            font-size: 0;
            &:before {
                content: "";
                display: block;
                width: 100%;
                height: 2px;
                background-color: #fff;
                position: absolute;
                border-radius: 1px;
                left: 0;
                bottom: -3px; }
            svg {
                path {
                    fill: #000;
                    transition: fill .25s ease-in-out; } } } }
    &__body {
        background-color: $grey-green;
        padding: 33px 16px 28px 29px;
        //padding: 28px 16px 28px 29px
        // flex-grow: 1
        // overflow-y: auto
        height: $headerHeightOffset;
        @include rorient(767, landscape) {
            display: none; }
        @include tablet-less-sm {
            display: none; }
        .ps {
            &__rail-y {
                // margin-right: 10px
                background: $grey-light;
                width: 4px;
                &.ps--clicking, &:focus, &:hover {
                    .ps__thumb-y {
                        width: 4px; } } }
            &__thumb-y {
                background: $grey-simple;
                width: 4px;
                right: 0; } } }

    &__list {
        position: relative;
        overflow: hidden;
        height: 100%;
        li {
            font-size: 16px;
            line-height: 1.375;
            @include hm-l;
            margin-bottom: 14px; }
        strong {
            font-size: 18px;
            line-height: 1.44;
            @include hm-sb;
            display: block; } } }




.marker {
    svg {
        transition: transform .3s ease-in-out;
        transform-origin: 50% 100%;
        path {
           transition: fill .3s ease-in-out; } }
    &.is-active {
        svg {
            transform: scale(1.5);
            path {
                fill: #000; } } } }
body {
    &.ie {
        .marker-popup__btn {
            //padding: 15px 5px 10px
            min-width: 72px;
            @include desktop-less-lg {
 }                //padding-top: 16px
            @include tablet-less-md {
                padding: 16px 6px 10px 8px;
                min-width: 61px; } } }

    &.firefox {
        .marker-popup__btn {
            @include gpu; } } }
